import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView'
import ProjectView from '../views/ProjectView'
import InteriorsView from '../views/InteriorsView'
import ContactView from '../views/ContactView'
import PressandReviewsView from '../views/PressandReviewsView'
import WorkshopView from '../views/WorkshopView'

Vue.use(VueRouter)

const title = "JETSAM MADE"

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta:{title:title}
  },
  {
    path: '/about',
    name: 'about',
    component:AboutView,
    meta:{title:"About | "+title}
  },
  {
    path: '/projects',
    name: 'projects',
    component:ProjectView,
    meta:{title:"Projects | "+title}
  },
  {
    path: '/Interiors',
    name: 'Interiors',
    component:InteriorsView,
    meta:{title:"Interiors | "+title}
  },
  {
    path: '/Contact',
    name: 'Contact',
    component:ContactView,
    meta:{title:"Contact | "+title}
  }
  ,
  {
    path: '/PressandReviewsView',
    name: 'PressandReviewsView',
    component:PressandReviewsView,
    meta:{title:"PressandReviews | "+title}
  }
  ,{
    path: '/WorkshopView',
    name: 'WorkshopView',
    component:WorkshopView,
    meta:{title:"Workshop | "+title}
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})



export default router

router.beforeEach((to,from,next) => {
  document.title = to.meta.title 
  next()
})